import React from 'react'
import styled from 'styled-components'

function Header() {
  return (
    <Nav>
      <Logo src='/images/logo.svg' />
      <NavMenu>
            <a>
                <img src='/images/home-icon.svg' />
                <span>HOME</span>
            </a>
            <a>
                <img src='/images/search-icon.svg' />
                <span>SEARCH</span>
            </a>
            <a>
                <img src='/images/watchlist-icon.svg' />
                <span>WATCH LIST</span>
            </a>
            <a>
                <img src='/images/movie-icon.svg' />
                <span>MOVIES</span>
            </a>
            <a>
                <img src='/images/series-icon.svg' />
                <span>SERIES</span>
            </a>
      </NavMenu>
      <UserImg src='https://staticg.sportskeeda.com/editor/2023/02/f6fd7-16762845387299-1920.jpg'/>
    </Nav>
  )
}

export default Header
const Nav = styled.nav({
    height: '70px',
    backgroundColor: '#090b13',
    display:'flex',
    alignItems:'center',
    padding:'0 36px'
});
const Logo = styled.img({
    width: '80px'
})
const NavMenu = styled.div({
    display:'flex',
    flex:1,
    marginLeft:'20px',
    alignItems:'center',
    a:{
        display:'flex',
        alignItems:'center',
        padding:'0px 12px',
        cursor:'pointer',
        img:{
            height:'20px'
        },
        span:{
            fontSize:'13px',
            letterSpacing:'1.42px',
            position:'relative',
            '&:after': {
                content:'""',
                height:'2px',
                backgroundColor:'white',
                position:'absolute',
                left:'0',
                right:'0',
                bottom:'-6px',
                opacity:0,
                transform:'scaleX(0)',
                transition: 'all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s',
                transformOrigin:'left center'

            }
        },
        '&:hover':{
            'span:after':{
                opacity:1,
                transform:'scaleX(1)'
            }
        }

    }
});
const UserImg = styled.img({
    height:'48px',
    width:'48px',
    borderRadius:'50%',
    cursor:'pointer'
})

